import axios from "axios";
import { useEffect, useState, useContext } from "react";
import {
  MessageContext,
  handleSuccessMessage,
  handleErrorMessage,
} from "../store/messageStore";

function CouponModal({ closeModal, getCoupons, type, tempCoupon }) {
  // 
  const [tempData, setTempData] = useState({
    // 
    title: "",
    is_enabled: 1,
    percent: 80,
    due_date: 1555459200,
    code: "testCode",
  });

  // 
  const [date, setDate] = useState(new Date());
  const [, dispatch] = useContext(MessageContext);

  useEffect(() => {
    if (type === "create") {
      setTempData({
        title: "",
        is_enabled: 1,
        percent: 80,
        due_date: 1555459200,
        code: "testCode",
      });
      setDate(new Date());
    } else if (type === "edit") {
      setTempData(tempCoupon);
      setDate(new Date(tempCoupon.due_date));
    }
  }, [type, tempCoupon]);

  // 
  const handleChange = (e) => {
    // name input  title ->  api title 
    // value 
    const { name, value } = e.target;

    //  input  value 
    // 
    //  name  includes 

    if (["price", "origin_price"].includes(name)) {
      setTempData({
        ...tempData,
        [name]: Number(value),
      });
    } else if (name === "is_enabled") {
      //  checkbox 
      setTempData({
        ...tempData,
        [name]: +e.target.checked, //  false  true  0  1
      });
    } else {
      setTempData({
        ...tempData, // 
        [name]: value, //  : 
      });
    }
  };

  //  post 
  const submit = async () => {
    try {
      // 
      let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon`;
      let method = "post";

      // 
      if (type === "edit") {
        api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon/${tempCoupon.id}`;
        method = "put";
      }

      const res = await axios[method](api, {
        data: {
          ...tempData,
          due_date: date.getTime(),
        }, //  api  data 
      });
      console.log(res);
      // submit
      handleSuccessMessage(dispatch, res); // 
      // 
      closeModal();
      //  props 
      getCoupons();
    } catch (error) {
      console.log(error);
      handleErrorMessage(dispatch, error); // 
    }
  };

  // /
  // const uploadFile = async (file) => {
  //   console.log(file);
  //   if (!file) return;

  //   const formData = new FormData();
  //   formData.append("file-to-upload", file);

  //   try {
  //     const res = await axios.post(
  //       `/v2/api/${process.env.REACT_APP_API_PATH}/admin/upload`,
  //       formData
  //     );
  //     console.log(res);
  //     if (res.data.success) {
  //       setTempData({
  //         ...tempData,
  //         imageUrl: res.data.imageUrl,
  //       });
  //     }
  //   } catch (error) {
  //     console.log(error);
  //   }
  // };

  return (
    <div
      className="modal fade"
      tabIndex="-1"
      id="productModal"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-lg">
        <div className="modal-content">
          <div className="modal-header">
            <h1 className="modal-title fs-5" id="exampleModalLabel">
              {type === "create"
                ? ""
                : ` ${tempCoupon.title}`}
            </h1>
            <button
              type="button"
              className="btn-close"
              aria-label="Close"
              onClick={closeModal}
            />
          </div>
          <div className="modal-body">
            <div className="mb-2">
              <label className="w-100" htmlFor="title">
                
                <input
                  type="text"
                  id="title"
                  placeholder=""
                  name="title"
                  className="form-control mt-1"
                  value={tempData.title}
                  onChange={handleChange}
                />
              </label>
            </div>
            <div className="row">
              <div className="col-md-6 mb-2">
                <label className="w-100" htmlFor="percent">
                  %
                  <input
                    type="text"
                    name="percent"
                    id="percent"
                    placeholder="%"
                    className="form-control mt-1"
                    value={tempData.percent}
                    onChange={handleChange}
                  />
                </label>
              </div>
              <div className="col-md-6 mb-2">
                <label className="w-100" htmlFor="due_date">
                  
                  <input
                    type="date"
                    id="due_date"
                    name="due_date"
                    placeholder=""
                    className="form-control mt-1"
                    value={`${date.getFullYear().toString()}-${(
                      date.getMonth() + 1
                    )
                      .toString()
                      .padStart(2, 0)}-${date
                      .getDate()
                      .toString()
                      .padStart(2, 0)}`}
                    onChange={(e) => {
                      setDate(new Date(e.target.value));
                    }}
                  />
                </label>
              </div>
              <div className="col-md-6 mb-2">
                <label className="w-100" htmlFor="code">
                  
                  <input
                    type="text"
                    id="code"
                    name="code"
                    placeholder=""
                    className="form-control mt-1"
                    value={tempData.code}
                    onChange={handleChange}
                  />
                </label>
              </div>
            </div>
            <label className="form-check-label" htmlFor="is_enabled">
              <input
                className="form-check-input me-2"
                type="checkbox"
                id="is_enabled"
                name="is_enabled"
                checked={!!tempData.is_enabled} // !!  
                onChange={handleChange}
              />
              
            </label>
          </div>
          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={closeModal}
            >
              
            </button>
            <button type="button" className="btn btn-primary" onClick={submit}>
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CouponModal;
