import { useEffect, useRef, useState, useContext } from "react";
import axios from "axios";
import CouponModal from "../../components/CouponModal";
import DeleteModal from "../../components/DeleteModal";
import { Modal } from "bootstrap";
import Pagination from "../../components/Pagination";

import {
  MessageContext,
  handleSuccessMessage,
  handleErrorMessage,
} from "../../store/messageStore";

function AdminCoupons() {
  const [coupons, setCoupons] = useState([]);
  const [pagination, setPagination] = useState({});

  // type:  modal 
  const [type, setType] = useState("create"); // create, edit

  //  tempProduct 
  const [tempCoupon, setTempCoupon] = useState({});

  //  useRef 
  const couponModal = useRef(null);
  const deleteModal = useRef(null);

  //  useContext  dispatch
  const [, dispatch] = useContext(MessageContext);

  //  useEffect ()
  useEffect(() => {
    //  productModal  useRef  Modal
    couponModal.current = new Modal("#productModal", {
      backdrop: "static", // 
    });

    deleteModal.current = new Modal("#deleteModal", {
      backdrop: "static",
    });

    // 
    getCoupons();
  }, []);

  //  page 
  const getCoupons = async (page = 1) => {
    // 

    //  products api all api 
    const couponRes = await axios.get(
      `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupons?page=${page}`
    );
    console.log(couponRes);

    setCoupons(couponRes.data.coupons);
    setPagination(couponRes.data.pagination);
  };

  // 
  const openCouponModal = (type, item) => {
    //  type  tempProduct
    //  props  ProductModal
    setType(type);
    setTempCoupon(item);
    couponModal.current.show();
  };

  const closeModal = () => {
    couponModal.current.hide();
  };

  // 
  const openDeleteModal = (product) => {
    //   tempProduct
    //  props  ProductModal
    setTempCoupon(product);
    deleteModal.current.show();
  };

  const closeDeleteModal = () => {
    deleteModal.current.hide();
  };

  // 
  const deleteCoupon = async (id) => {
    try {
      const res = await axios.delete(
        `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon/${id}`
      );
      if (res.data.success) {
        getCoupons();
        handleSuccessMessage(dispatch, res);
        closeDeleteModal();
      }
    } catch (error) {
      console.error(error);
      handleErrorMessage(dispatch, error);
    }
  };

  return (
    <div className="p-3">
      {/*  */}
      <CouponModal
        closeModal={closeModal}
        getCoupons={getCoupons}
        tempCoupon={tempCoupon}
        type={type}
      />

      {/*  */}
      <DeleteModal
        close={closeDeleteModal}
        text={tempCoupon.title}
        handleDelete={deleteCoupon}
        id={tempCoupon.id}
      />

      <h3></h3>
      <hr />
      <div className="text-end">
        <button
          type="button"
          className="btn btn-primary btn-sm"
          onClick={() => openCouponModal("create", {})}
        >
          
        </button>
      </div>
      <table className="table">
        <thead>
          <tr>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          {coupons.map((product) => {
            return (
              <tr key={product.id}>
                <td>{product.title}</td>
                <td>{product.percent}</td>
                <td>{new Date(product.due_date).toDateString()}</td>
                <td>{product.code}</td>
                <td>{product.is_enabled ? "" : ""}</td>
                <td>
                  <button
                    type="button"
                    className="btn btn-primary btn-sm"
                    onClick={() => openCouponModal("edit", product)}
                  >
                    
                  </button>
                  <button
                    type="button"
                    className="btn btn-outline-danger btn-sm ms-2"
                    onClick={() => openDeleteModal(product)}
                  >
                    
                  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>

      {/*  */}
      <Pagination pagination={pagination} changePage={getCoupons} />
    </div>
  );
}

export default AdminCoupons;
